import $ from "jquery";

export default {
  name: "CusDashboardView",
  props: ["dataHutang", "ipAdress"],

  data() {
    return {
      user: JSON.parse(localStorage.getItem("SessionUser")),
      info_pembayaran_hutang: false,
      info_pembayaran_d_saldo: false,
      jumlahDepositSaldo: null,
      nowDate: null,
      lastDate: null,
      lastDateConvert: null,
      currentDate: null,
      diffDays: null,
    };
  },

  methods: {
    tarikCoin() {
      axios
        .post(
          this.ipAdress +
            "api/transaksi/tarik-coin?username=" +
            this.user.username
        )
        .then((response) => {
          Swal.fire({
            icon: response.data.status,
            title: response.data.message,
            text: response.data.keterangan,
            showConfirmButton: false,
            timer: 1500,
          }).then(function () {
            window.location = "/customer/dashboard";
          });
        });
    },
    numberFormat(x) {
      if (x) {
        return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
      } else {
        return (x = "0");
      }
    },
    format_date(value) {
      if (value) {
        return moment(String(value)).format("DD MMMM YYYY HH:mm");
      }
    },
    getNow() {
      const today = new Date();
      const date = today.getFullYear() + "-" + (today.getMonth() + 1);
      this.nowDate = date;

      const oneDay = 24 * 60 * 60 * 1000; // hours*minutes*seconds*milliseconds
      const lastDate = new Date(2023, 0, 2);
      const lastDateConv =
        lastDate.getFullYear() + "-" + (lastDate.getMonth() + 1);
      this.lastDate = lastDateConv;
      const currentDate = new Date();

      const month = [
        "Januari",
        "Februari",
        "Maret",
        "April",
        "Mei",
        "Juni",
        "Juli",
        "Agustus",
        "September",
        "Oktober",
        "November",
        "Desember",
      ];
      this.lastDateConvert =
        lastDate.getDate() +
        " " +
        month[lastDate.getMonth()] +
        " " +
        lastDate.getFullYear();
      this.currentDate = currentDate;

      const diffDays = Math.round((lastDate - currentDate) / oneDay);
      this.diffDays = diffDays + 1;
    },
  },

  mounted() {
    if (this.nowDate === this.lastDate) {
      $("#Pengumuman").modal("show");
    }
  },

  created() {
    this.getNow();
    // setInterval(this.getNow, 1000);
  },

  computed: {
    orderedDataHutang: function () {
      return _.orderBy(this.dataHutang.data_hutang, "created_at", "desc");
    },
  },
};
</script>

